import Toast from '../../wxcomponents/vant/dist/toast/toast';
export default {
	data() {
		return {
			id:'',
			price:'',
			tabList:['电子发票','纸质发票'],
			n:0,
			company_name:'',
			taxes_code:'',
			company_address:'',
			company_account:'',
			company_bank:'',
			mobile:'',
			recipient:'',
			send_address:'',
			email:'',
			show:false,
			show1:false
		}
	},
	onLoad(e) {
		this.id = e.id
		this.price = e.price
	},
	methods: {
		send2(){
			this.$api.apply_invoice({
				token: uni.getStorageSync("token"),
				user_id: uni.getStorageSync("userId"),
				order_id:this.id,
				company_name:this.company_name,
				taxes_code:this.taxes_code,
				company_address:this.company_address,
				company_account:this.company_account,
				company_bank:this.company_bank,
				send_address:this.send_address,
				recipient:this.recipient,
				mobile:this.mobile,
				invoice_money:this.price
			}).then(res=>{
				Toast(res.data.msg)
				if(res.data.code=="200"){
					setTimeout(function() {
						uni.redirectTo({
						    url: '../../pagesThree/InvoiceSubmit/InvoiceSubmit'
						});
					}, 1000);
				}
			})
		},
		send1(){
			// uni.redirectTo({
			//     url: '../../pagesThree/InvoiceSubmit/InvoiceSubmit'
			// });
			this.$api.apply_invoice_electronic({
				token: uni.getStorageSync("token"),
				user_id: uni.getStorageSync("userId"),
				order_id:this.id,
				company_name:this.company_name,
				taxes_code:this.taxes_code,
				company_address:this.company_address,
				company_account:this.company_account,
				company_bank:this.company_bank,
				email:this.email,
				invoice_money:this.price
			}).then(res=>{
				Toast(res.data.msg)
				if(res.data.code=="200"){
					setTimeout(function() {
						uni.redirectTo({
						    url: '../../pagesThree/InvoiceSubmit/InvoiceSubmit'
						});
					}, 1000);
				}
			})
		},
		tab(index){
			this.n = index
		},
		submit(){
			if(this.n==0){
				var that = this	      
				if (this.company_name && this.taxes_code && this.email) {
					this.show = true
				} else {
				    if (!this.company_name) {
				      Toast('发票抬头没有填写')
				    return
				  }
					if (!this.taxes_code) {
					  Toast('税号没有填写')
					  return
					}
					if (!this.email) {
					  Toast('邮箱没有填写')
					  return
					}
				}
			}else{
				if (this.company_name && this.taxes_code && this.recipient && this.mobile && this.send_address) {
					this.show1 = true
				} else {
				    if (!this.company_name) {
				      Toast('发票抬头没有填写')
				    return
				  }
					if (!this.taxes_code) {
					  Toast('税号没有填写')
					  return
					}
					if (!this.recipient) {
					  Toast('收件人没有填写')
					  return
					}
					if (!this.mobile) {
					  Toast('联系电话')
					  return
					}
					if (!this.send_address) {
					  Toast('邮寄地址')
					  return
					}
				}
			}
		},
		close(){
			this.show = false
			this.show1 = false
		}
	}
}
